    line-height: 24px;
    font-family: 'Helvetica Neue', 'Helvetica', Helvetica, Arial, sans-serif;
    color: #555;
    table {
      width: 100%;
      line-height: inherit;
      text-align: left;
      td {
          padding: 5px;
          vertical-align: top;
          &:nth-child(2) {
              text-align: right;
          }
      }
      tr{
        &.top{
          table{
            td{
              padding-bottom:20px;
              &.title{
                font-size:45px;
                line-height: 45px;
                color: #333;                    
              }
            }
          }

        }
        &.information{
          table{
            td{
              padding-bottom:40px;
            }
          }
        }
        &.heading{
          td{
         background: #eee;
        border-bottom: 1px solid #ddd;
        font-weight: bold;               
          }
        }
        &.details{
          td{
            padding-bottom:20px;
          }
        }
        &.item{
          &.last{
            td{
              border-bottom:none;
            }
          }
          td{
     border-bottom: 1px solid #eee;

          }
        }
        &.total{
          td{
            &:nth-child(2){
     border-top: 2px solid #eee;
    font-weight: bold;                 
            }
          }
        }
      }
    }      
}

